import { request } from './request'

export function login(phone, key) {
    return request({
        url: `/login/cellphone?phone=${phone}&password=${key}`,
        params: {
            timestamp: new Date().getTime(),
        },
    })
}

export function verify(phone) {
    return request({
        url: `/cellphone/existence/check?phone=${phone}`,
        params: {
            timestamp: new Date().getTime(),
        },
    })
}

/**
 * key
 */
export function loginQrCodeKey() {
    return request({
        url: '/login/qr/key',
        method: 'get',
        params: {
            timestamp: new Date().getTime(),
        },
    });
}

/**
 * 
 * : keybase64,
 * base64,
 * @param {Object} params
 * @param {string} params.key
 * @param {string=} params.qrimg base64
 */
export function loginQrCodeCreate(params) {
    return request({
        url: '/login/qr/create',
        method: 'get',
        params: {
            ...params,
            timestamp: new Date().getTime(),
        },
    });
}

/**
 * 
 * : ,800,801,802,803(803cookies)
 * @param {string} key
 */
export function loginQrCodeCheck(key) {
    return request({
        url: '/login/qr/check',
        method: 'get',
        params: {
            key,
            timestamp: new Date().getTime(),
        },
    });
}